import React from 'react'
import "./Articlebox.css"

function Articlebox({ topic, title, color}) {
    return (
        <div style={{color: {color}}} className="articleBox">
            <div className="articleBox__title">
                <h6>{ topic }</h6>
                <h2>{ title }</h2>
            </div>
        </div>
    )
}


export default Articlebox
